html {
  width: 100vw;
  height: 100vh;
}

body {
  font-family: 'Chelsea Market', sans-serif;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

#root {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
}

#drum-machine {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
}

#intro-screen {
  position: relative;
  padding-top: 10vh;
  background-color: rgba(32, 178, 170, 0.5);
  background-repeat: repeat;
  width: 100%;
  height: 100%;
  max-width: 100vw;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: stretch;
}

#intro-interact {
  padding: 0;
  margin: 0;
  margin-top: 20px;
  margin-right: 40px;
  width: 500px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-evenly;
  align-items: stretch;
}

#intro-dialogue {
  width: 500px;
  font-family: inherit;
  display: flex;
  flex-flow: column nowrap;
  align-items: flex-start;
  justify-content: flex-start;
}

.intro-line {
  text-align: start;
  font-size: 18px;
}

.pulsing {
  animation-name: pulse;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
}

@keyframes pulse {
  0% {
    color: rgba(0, 0, 128, 1);
  }
  50% {
    color: rgba(0, 0, 0, 0);
  }
  100% {
    color: rgba(0, 0, 0, 1)
  }
}

#skip-int {
  position: absolute;
  top: 10px;
  right: 10px;
  font-weight: bold;
  background-color: rgba(32, 178, 170, 0);
  border-style: none;
}


.hidden {
  visibility: hidden;
}

#floyd-box {
  align-self: flex-end;
  
}
#floyd-img {
  width: 300px;
  height: 400px;
}

#main-screen {
  background-image: url(https://cdn.pixabay.com/photo/2016/12/24/23/50/pattern-1929506_960_720.png);
  background-size: 5%;
  background-repeat: repeat;
  position: relative;
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

#ready {
  position: absolute;
  bottom: 20px;
  right: 20px;
  background-color: hsl(0, 0%, 66%);
  border: 2px black solid;
}

#machine-main {
  padding-left: 18px;
  padding-right: 25px;
  padding-bottom: 25px;
  margin: auto;
  background-color:#a9a9a9;
  border-radius: 5px;
  width: 800px;
  border: 2px solid black;
  box-shadow: 0 .5rem .6rem .5rem rgba(0, 0, 0, 0.5);
  height: 400px;
  display: grid;
  grid-template-rows: 75px 300px;
  grid-template-columns: 300px 150px 300px;
  grid-template-areas: 
    "title title title"
    "drum display other";
  grid-gap: 5px;
}

.drum-pad, .other-pad, .center-button {
  background-color: #D3D3D3;
  font-family: 'Bungee', sans-serif;
  border: none;
  border: outset #a9a9a9;
  font-size: 20px;
}

#title {
  grid-area: title;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-evenly;
  align-items: center;
  font-family: 'Faster One', sans-serif;
}

#drum-panel {
  grid-area: drum;
  display: grid;
  grid-template-rows: 100px 100px 100px;
  grid-template-columns: 75px 75px 75px 75px;
}

#drum-icon {
  grid-area: 2/4/3/5;
  font-size: 30px;
  margin: auto;
}

#Hi-hat {
  grid-area: 1/2/2/3;
}

#Ride {
  grid-area: 1/3/2/4;
}

#Crash {
  grid-area: 1/4/2/5;
}

#High-tom {
  grid-area: 2/1/3/2;
}

#Mid-tom {
  grid-area: 2/2/3/3;
}

#Low-tom {
  grid-area: 2/3/3/4;
}

#Snare {
  grid-area: 3/2/4/3;
}

#BassD {
  grid-area: 3/3/4/4;
}

#Drumsticks {
  grid-area: 3/4/4/5;
}

#center-panel {
  grid-area: display;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-around;
  align-items: center;
}

#display {
  background-color: black;
  width: 75px;
  height: 50px;
  text-align: center;
  color: green;
  font-family: 'Bungee', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
}

#display p {
  padding-top: 10px;
}

.fa-circle {
  color: brown;
}

#other-panel {
  grid-area: other;
  display: grid;
  grid-template-rows: 100px 100px 100px;
  grid-template-columns: 75px 75px 75px 75px;
}

#other-icon {
  grid-area: 2/1/3/2;
  font-size: 30px;
  margin: auto;
}

#BassG {
  grid-area: 1/1/2/2;
}

#Beep {
  grid-area: 1/2/2/3;
}

#Boing {
  grid-area: 1/3/2/4;
}

#Chirp {
  grid-area: 2/2/3/3;
}

#Chord {
  grid-area: 2/3/3/4;
}

#Drop {
  grid-area: 2/4/3/5;
}

#Glass {
  grid-area: 3/1/4/2;
}

#Whoosh {
  grid-area: 3/2/4/3;
}

#Woah {
  grid-area: 3/3/4/4;
}

#performance {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: black;
  z-index: 1;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
}

#return {
  position: absolute;
  top: 20px;
  left: 20px;
  background-color: #a9a9a9;
  border: 2px black solid;
  z-index: 3;
}

#bubbles {
  z-index: 2;
  width: 100%;
  height: 50%;
  position: relative;
}

#bubble1 {
  position: absolute;
  left: 10px;
  top: 25%;
  animation-name: bubble-anim;
  animation-duration: 1.5s;
  animation-iteration-count: 1;
  animation-fill-mode: both;
  animation-delay: 2s;
}

#bubble2 {
  position: absolute;
  top: 45%;
  right: 20%;
  left: 50%;
  animation-name: bubble-anim;
  animation-duration: 1.5s;
  animation-iteration-count: 1;
  animation-fill-mode: both;
  animation-delay: 8s;
}

#bubble3 {
  position: absolute;
  top: 30%;
  right: 15%;
  animation-name: bubble-anim;
  animation-duration: 1.5s;
  animation-iteration-count: 1;
  animation-fill-mode: both;
  animation-delay: 15s;
}

@keyframes bubble-anim {
  0% {
    display: hidden;
  }

  1% {
    width: 20px;
    height: 20px;
    border-radius: 100%;
    background-color: white;
    opacity: 1;
  }

  99% {
    width: 200px;
    height: 200px;
    border-radius: 100%;
    opacity: 0.1;
  }

  100% {
    display: hidden;
  }
}

#machine-main-bottom {
  z-index: 3;
  padding-left: 18px;
  padding-right: 25px;
  padding-bottom: 25px;
  margin: auto;
  background-color:#a9a9a9;
  border-radius: 5px;
  width: 800px;
  border: 2px solid black;
  height: 400px;
  position: absolute;
  bottom: 20px;
  left: 50%;
  left: -400px;
  right: 50%;
  right: -400px;
  display: grid;
  grid-template-rows: 75px 300px;
  grid-template-columns: 300px 150px 300px;
  grid-template-areas: 
    "title title title"
    "drum display other";
  grid-gap: 5px;
}

#performance-interact {
  width: 100%;
  height: 50%;
  padding-bottom: 40px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

#performance-dialogue {
  font-size: 18px;
  width: 500px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

#performance-line {
  text-align: center;
  color: white;
  font-family: inherit;
}

.pulsing-dark {
  animation-name: dark-pulse;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  color: white;
}

@keyframes dark-pulse {
  0% {
    color: white;
  }
  50% {
    color: rgba(255, 255, 255, 0);
  }
  100% {
    color: rgba(255, 255, 255, 1)
  }
}

#song-options {
  width: 800px;
  margin-bottom: 20px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-evenly;
  align-items: center;
}

.song-button {
  width: 20%;
  background: none;
  color: white;
  border-color: white;
  border-style: solid;
}

#final-screen {
  height: 100%;
  width: 100%;
  background-color: black;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  align-items: flex-end;
  color: white;
}

#final-dialogue {
  font-size: 18px;
  align-self: center;
  margin-right: 30vw;
}

#credits0, #credits1, #credits2, #credits3 {
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  align-self: center;
}

#final-img {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 300px;
  height: 400px;
}

#final-line0 {
  animation-name: final-anim1;
  animation-duration: 10s;
}

#final-line1{
  animation-name: final-anim2;
  animation-duration: 10s;
}

#final-line2{
  animation-name: final-anim3;
  animation-duration: 10s;
}

#final-line3 {
  animation-name: final-anim4;
  animation-duration: 10s;
}

#credits0 {
  animation-name: final-anim5;
  animation-duration: 15s;
}

#credits1 {
  animation-name: final-anim6;
  animation-duration: 15s;
}

#credits2 {
  animation-name: final-anim;
  animation-duration: 15s;
  animation-fill-mode: forwards;
}

@keyframes final-anim1 {
  0% {
    color: black;
  }

  10% {
    color: white;
  }

  90% {
    color: white;
  }

  100% {
    color: black;
  }
}

@keyframes final-anim2 {
  0% {
    color: black;
  }

  10% {
    color: white;
  }

  90% {
    color: white;
  }

  100% {
    color: black;
  }
}

@keyframes final-anim3 {
  0% {
    color: black;
  }

  10% {
    color: white;
  }

  90% {
    color: white;
  }

  100% {
    color: black;
  }
}

@keyframes final-anim4 {
  0% {
    color: black;
  }

  10% {
    color: white;
  }

  90% {
    color: white;
  }

  100% {
    color: black;
  }
}

@keyframes final-anim5 {
  0% {
    color: black;
  }

  10% {
    color: white;
  }

  90% {
    color: white;
  }

  100% {
    color: black;
  }
}

@keyframes final-anim6 {
  0% {
    color: black;
  }

  10% {
    color: white;
  }

  90% {
    color: white;
  }

  100% {
    color: black;
  }
}

@keyframes final-anim {
  from {
    color: black;
  }

  to {
    color: white;
  }


}

@media screen and (max-width: 800px) {

html {
  min-width: 100vw;
  min-height: 100vh;
  min-height: -webkit-fill-available;
}

#intro-screen {
  overflow: scroll;
  padding-top: 0;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

#intro-interact {
  margin: 0;
  padding: 0;
  max-width: 90vw;
  height: 200px;
  flex-flow: column nowrap;
  justify-content: space-evenly;
  align-items: center;
  order: 1;
}

#intro-dialogue {
  align-self: center;
  border: 2px solid black;
