  padding-top: 18px;
  padding-bottom: 25px;
  padding-left: 25px;
  width: 340px;
  height: 800px;
  position: static;
  margin-bottom: 20px;
  grid-template-rows: 300px 150px 300px;
  grid-template-columns: 250px 65px;
  grid-template-areas: 
    "drum title"
